import React from 'react';
import './SubmitButton.css';


function SubmitButton( {doNumber}){
    return (
        <div className="submitButton">
            <button name="submitButton"
                 id="submitButton" 
                 class="btn btn-primary" 
                 type="button" 
                 onClick = {doNumber}>
                     Ingresar Al sistema
            </button>
        </div>
    )
}

export default SubmitButton;